import { useRouter } from 'next/router'
import { useState } from 'react'
import axios from 'axios'
import toast from 'react-hot-toast'
import { Zap, Crown, Check, Shield, Star, Sparkles } from 'lucide-react'

export default function PremiumPage() {
    const router = useRouter()
    const [loading, setLoading] = useState(false)

    async function handleUpgrade() {
        const token = localStorage.getItem('token')
        if (!token) return toast.error('Please login first')

        setLoading(true)
        try {
            const res = await axios.post('/api/stripe/checkout', { token })
            if (res.data.url) {
                window.location.href = res.data.url
            }
        } catch (e) {
            toast.error('Payment failed')
        } finally {
            setLoading(false)
        }
    }

    const plans = [
        {
            name: 'Free',
            price: '$0',
            tag: 'Basic',
            features: ['Receive Anonymous Messages', 'Standard Inbox', 'Story Sharing'],
            button: 'Current Plan',
            premium: false
        },
        {
            name: 'Pro',
            price: '$4.99',
            tag: 'Recommended',
            features: ['Reveal sender (if they consent)', 'Priority Support', 'Custom Branding', 'Ad-free Experience', 'Advanced Analytics'],
            button: 'Upgrade to Pro',
            premium: true
        }
    ]

    return (
        <div style={{ minHeight: '100vh', padding: '60px 20px', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <div className="grid-bg" style={{ position: 'fixed', inset: 0, zIndex: -1 }}></div>

            <div style={{ textAlign: 'center', marginBottom: '60px' }}>
                <div style={{ display: 'inline-flex', alignItems: 'center', gap: '10px', background: 'rgba(204, 255, 0, 0.1)', color: 'var(--accent-lime)', padding: '10px 20px', borderRadius: '100px', fontSize: '14px', fontWeight: 800, marginBottom: '20px' }}>
                    <Crown size={18} /> GO PREMIUM
                </div>
                <h1 style={{ fontSize: 'clamp(32px, 5vw, 64px)', fontWeight: 800, margin: '0 0 20px 0' }}>Unlock the <span className="text-gradient-primary">Full Potential</span></h1>
                <p style={{ color: 'var(--text-secondary)', fontSize: '18px', maxWidth: '600px' }}>
                    Discover who's behind the secrets. Get insights and aesthetic tools to level up your DesiNGL experience.
                </p>
            </div>

            <div style={{ display: 'flex', gap: '30px', flexWrap: 'wrap', justifyContent: 'center', maxWidth: '1000px', width: '100%' }}>
                {plans.map((p, i) => (
                    <div key={i} style={{
                        flex: 1,
                        minWidth: '300px',
                        maxWidth: '400px',
                        background: p.premium ? 'rgba(138, 43, 226, 0.05)' : 'var(--glass-bg)',
                        border: `1px solid ${p.premium ? 'var(--accent-purple)' : 'var(--glass-border)'}`,
                        borderRadius: '32px',
                        padding: '40px',
                        display: 'flex',
                        flexDirection: 'column',
                        boxShadow: p.premium ? '0 20px 50px rgba(138, 43, 226, 0.2)' : 'none',
                        position: 'relative'
                    }}>
                        {p.premium && (
                            <div style={{ position: 'absolute', top: '20px', right: '20px', background: 'var(--accent-purple)', color: '#fff', fontSize: '11px', fontWeight: 800, padding: '4px 12px', borderRadius: '100px' }}>
                                MOST POPULAR
                            </div>
                        )}

                        <h3 style={{ fontSize: '20px', color: 'var(--text-secondary)', margin: '0 0 10px 0' }}>{p.tag}</h3>
                        <h2 style={{ fontSize: '32px', margin: '0 0 10px 0' }}>{p.name}</h2>
                        <div style={{ display: 'flex', alignItems: 'baseline', gap: '5px', marginBottom: '30px' }}>
                            <span style={{ fontSize: '48px', fontWeight: 800 }}>{p.price}</span>
                            <span style={{ color: 'var(--text-secondary)' }}>/month</span>
                        </div>

                        <div style={{ flex: 1, marginBottom: '40px' }}>
                            {p.features.map((f, fi) => (
                                <div key={fi} style={{ display: 'flex', alignItems: 'center', gap: '12px', marginBottom: '15px' }}>
                                    <div style={{ background: p.premium ? 'var(--accent-purple)' : 'var(--glass-border)', padding: '4px', borderRadius: '50%' }}>
                                        <Check size={14} color="#fff" />
                                    </div>
                                    <span style={{ color: 'var(--text-primary)', fontSize: '15px' }}>{f}</span>
                                </div>
                            ))}
                        </div>

                        <button
                            disabled={p.premium && loading}
                            onClick={p.premium ? handleUpgrade : null}
                            style={{
                                width: '100%',
                                padding: '18px',
                                borderRadius: '16px',
                                background: p.premium ? 'var(--accent-purple)' : 'transparent',
                                color: '#fff',
                                border: p.premium ? 'none' : '1px solid var(--glass-border)',
                                fontWeight: 800,
                                fontSize: '16px',
                                cursor: p.premium ? 'pointer' : 'default',
                                transition: 'all 0.2s',
                            }}
                            onMouseEnter={e => p.premium && !loading && (e.currentTarget.style.transform = 'translateY(-2px)')}
                            onMouseLeave={e => p.premium && !loading && (e.currentTarget.style.transform = 'translateY(0)')}
                        >
                            {loading && p.premium ? 'Processing...' : p.button}
                        </button>
                    </div>
                ))}
            </div>

            <div style={{ marginTop: '80px', textAlign: 'center' }}>
                <p style={{ color: 'var(--text-secondary)', display: 'flex', alignItems: 'center', gap: '10px', fontSize: '14px' }}>
                    <Shield size={16} /> Secure payment powered by Stripe. Cancel anytime.
                </p>
            </div>
        </div>
    )
}
